.tools {
	-webkit-flex-basis: 0;
	        flex-basis: 0;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.bag {
	padding: 1vmin;
}

.aspect {
	display: grid;
}

.aspect > * {
	grid-area: 1 / 1 / 2 / 2;
}

.board {
	width: 25rem;
	display: grid;
	grid-template-columns: repeat(15, calc((100% - 14px) / 15));
	grid-template-rows: repeat(15, auto);
	grid-gap: 1px;
}

.board-field {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	background-color: #444;
	color: white;
	text-align: center;
	font-size: 0.5rem;
	border: 1px solid transparent;
	cursor: pointer;
	white-space: pre-wrap;
}

.board-field.is-start,
.board-field.is-double-word {
	background-color: indianred;
}

.board-field.is-triple-word {
	background-color: crimson;
}

.board-field.is-double-letter {
	background-color: cornflowerblue;
}

.board-field.is-triple-letter {
	background-color: royalblue;
}

.board-field.is-selected {
	border-color: hotpink;
}

.tile {
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	border: 1px solid burlywood;
	border-bottom-width: 0.2em;
	padding: 0 0.2em 0.2em;
	border-radius: 0.4em;
	background-color: antiquewhite;
	color: black;
}
.tile.is-owned {
	background-color: white;
}
.tile.is-joker .tile-letter {
	font-style: italic;
}

.tile-letter {
	font-family: serif;
	font-size: 1.7em;
}

.tile-score {
	position: absolute;
	bottom: 5%;
	right: 5%;
	font-size: 0.5em;
}

.hand {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	padding: 0 1px 1px 0;
}

.hand-slot {
	width: calc((100vmin - 1px) / 7 - 1px);
	margin: 1px 0 0 1px;
	background: #444;
	padding: 1vmin;
	border: 1px solid transparent;
}

.hand-slot.is-selected {
	border-color: hotpink;
}

.hand-slot.is-to-be-replaced {
	opacity: 0.5;
}

.buttons {
	padding: 1vmin;
}

.word-info {
	padding: 1vmin;
}

.invalid-word {
	font-style: italic;
}

.valid-word {
	font-weight: bold;
}

.word-score {
	font-size: smaller;
}

.bingo {
	font-weight: bold;
	color: hotpink;
}

* {
	box-sizing: border-box;
}

html {
	font-family: sans-serif;
	font-size: 4vmin;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	background-color: #222;
	color: white;
}

#root {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

@media (min-aspect-ratio: 4/3) {
	#root {
		-webkit-flex-direction: row;
		        flex-direction: row;
	}
}

a {
	color: skyblue;
}

a:visited {
	color: violet;
}

button {
	background: -webkit-linear-gradient(white 1px, #666 1px, #777);
	background: linear-gradient(white 1px, #666 1px, #777);
	color: white;
	border-width: 0.5vmin;
	border-style: solid;
	border-top-color: #bbb;
	border-left-color: #999;
	border-right-color: #444;
	border-bottom-color: #333;
	text-shadow: -1px -1px 0 #333;
	padding: 0.5vmin 1vmin;
	position: relative;
}

button:active {
	background: -webkit-linear-gradient(bottom, #ccc 1px, #555 1px, #333);
	background: linear-gradient(to top, #ccc 1px, #555 1px, #333);
	border-bottom-color: #999;
	border-right-color: #777;
	border-left-color: #333;
	border-top-color: #111;
}

button:focus {
	text-shadow: 0 0 1vmin white;
}

button:disabled {
	text-shadow: -1px -1px 0 #333;
	background: #666;
	color: #bbb;
	border-color: transparent;
}

button::before,
button::after {
	position: absolute;
	content: '';
	left: -0.25vmin;
	right: -0.25vmin;
	height: 0.5vmin;
	-webkit-transform: skewX(45deg);
	        transform: skewX(45deg);
}
button::before {
	top: -0.5vmin;
	background: -webkit-linear-gradient(
		left,
		white 1px,
		rgba(255, 255, 255, 0) 1px
	);
	background: linear-gradient(
		to right,
		white 1px,
		rgba(255, 255, 255, 0) 1px
	);
}
button::after {
	bottom: -0.5vmin;
	background: -webkit-linear-gradient(right, black 1px, rgba(0, 0, 0, 0) 1px);
	background: linear-gradient(to left, black 1px, rgba(0, 0, 0, 0) 1px);
}

button:active::before {
	background: -webkit-linear-gradient(left, black 1px, rgba(0, 0, 0, 0) 1px);
	background: linear-gradient(to right, black 1px, rgba(0, 0, 0, 0) 1px);
}
button:active::after {
	background: -webkit-linear-gradient(right, #ccc 1px, rgba(204, 204, 204, 0) 1px);
	background: linear-gradient(to left, #ccc 1px, rgba(204, 204, 204, 0) 1px);
}

button:disabled::before,
button:disabled::after {
	content: none;
}

